import React, { Component } from 'react';
import { BrowserRouter as Router, Switch, Route, browserHistory, Link } from 'react-router-dom';
import logo from '../img/Fairuz-kun_logo_2020_4_512px.png';

export default class Header extends Component {
  constructor(props) {
    super(props)
    this.state = {headColor: ''}
  }

  
  Color1 = () => {
    this.setState({
      headColor: 'color1'
    })
  }

  Color2 = () => {
    this.setState({
      headColor: 'color2'
    })
  }

  Color3 = () => {
    this.setState({
      headColor: 'color3'
    })
  }


  render() {
    return (
      <header id="navbar" className="sticky">
          <div className="header">
            <Link onClick={this.Color1} to={'/'}>
              <div className="logo">
                <img src={logo} alt=""/>
                <div className="logo-text">Fairu-zu_</div>
              </div>
            </Link>
            <nav>
                <Link onClick={this.Color1} to={'/'} className="nm"><div>Home</div></Link>
                <Link onClick={this.Color2} to={'/artwork'} className="nm"><div>Artwork</div></Link>
                <Link onClick={this.Color3} to={'/about'} className="nm"><div>Arbout/Contact</div></Link>
            </nav>
          </div>
        </header>
      
    );
  }
}
